<template>
    <MaterialButton @click="handleClick" :disabled="disabled" :value="label"></MaterialButton>
</template>

<script>
import MaterialButton from './MaterialButton';
export default {
    props: {
        idle: {required: true},
        loading: {required: true},
        success: {required: true},
        failed: {required: true},
        action: {required: true}
    },
    methods: {
        handleClick() {
            console.log(this.action);
            this.$store.dispatch(this.action);
        },
    },
    computed: {
        disabled() {
            return this.$store.getters.getActionDisabled(this.action)
        },
        value() {
            return this.$store.getters.getActionStatus(this.action)
        },
        label() {
            return this[this.value];
        }
    },
    components: {
        MaterialButton
    }
}
</script>