import Vue from 'vue';
import Vuex from 'vuex';
import { ajaxCall } from './utils/ajax';

Vue.use(Vuex);

const ajaxData = JSON.parse(window.udeslyThemeData);

export default new Vuex.Store({
  state: {
    importMissingData: {
      disabled: false,
      status: 'idle',
    },
    deletePages: {
      disabled: false,
      status: 'idle',
    },
    deleteFrontendEditorData: {
      disabled: false,
      status: 'idle',
    },
    clearFrontendEditor: {
      disabled: false,
      status: 'idle',
    },
    missingPages: ajaxData.missingPages
  },
  mutations: {
    CHANGE_ACTIONS_STATUS(state, payload) {
      const oldState = state[payload.action];
      state[payload.action] = { ...oldState, ...payload.value };
    },
    CHANGE_STATE(state, payload) {
      state[payload.key] = payload.value;
    }
  },
  getters: {
    getActionStatus: state => action => {
      return state[action].status;
    },
    getActionDisabled: state => action => {
      return state[action].disabled;
    }
  },
  actions: {
    importMissingData({ commit }) {
      commit('CHANGE_ACTIONS_STATUS', {
        action: 'importMissingData',
        value: { disabled: true, status: 'loading' }
      });
      ajaxCall(ajaxData.ajaxurl, 'udesly_import_missing_data', ajaxData.nonce)
        .then(res => {
          if (res.ok) {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'importMissingData',
              value: { disabled: true, status: 'success' }
            });
            commit('CHANGE_STATE', {key: 'missingPages', value: 0});
          } else {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'importMissingData',
              value: { disabled: true, status: 'failed' }
            });
          }
        })
        .finally(() => {
          setTimeout(function() {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'importMissingData',
              value: { disabled: false, status: 'idle' }
            });
          }, 1500);
        });
    },
    deletePages({ commit }) {
      commit('CHANGE_ACTIONS_STATUS', {
        action: 'deletePages',
        value: { disabled: true, status: 'loading' }
      });
      ajaxCall(ajaxData.ajaxurl, 'udesly_delete_pages', ajaxData.nonce)
        .then(res => {
          if (res.ok) {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'deletePages',
              value: { disabled: true, status: 'success' }
            });
            res.json().then( data => {
                if (data.data.missingPages) {
                  commit('CHANGE_STATE', {key: 'missingPages', value: data.data.missingPages});
                }
            });
           
          } else {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'deletePages',
              value: { disabled: true, status: 'failed' }
            });
          }
        })
        .finally(() => {
          setTimeout(function() {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'deletePages',
              value: { disabled: false, status: 'idle' }
            });
          }, 1500);
        });
    },
    deleteFrontendEditorData({ commit }) {
      commit('CHANGE_ACTIONS_STATUS', {
        action: 'deleteFrontendEditorData',
        value: { disabled: true, status: 'loading' }
      });
      ajaxCall(ajaxData.ajaxurl, 'udesly_delete_frontend_editor_data', ajaxData.nonce)
        .then(res => {
          if (res.ok) {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'deleteFrontendEditorData',
              value: { disabled: true, status: 'success' }
            });
            
          } else {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'deleteFrontendEditorData',
              value: { disabled: true, status: 'failed' }
            });
          }
        })
        .finally(() => {
          setTimeout(function() {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'deleteFrontendEditorData',
              value: { disabled: false, status: 'idle' }
            });
          }, 1500);
        });
    },
    clearFrontendEditor({ commit }) {
      commit('CHANGE_ACTIONS_STATUS', {
        action: 'clearFrontendEditor',
        value: { disabled: true, status: 'loading' }
      });
      ajaxCall(ajaxData.ajaxurl, 'udesly_clear_frontend_editor_transient', ajaxData.nonce)
        .then(res => {
          if (res.ok) {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'clearFrontendEditor',
              value: { disabled: true, status: 'success' }
            });
            
          } else {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'clearFrontendEditor',
              value: { disabled: true, status: 'failed' }
            });
          }
        })
        .finally(() => {
          setTimeout(function() {
            commit('CHANGE_ACTIONS_STATUS', {
              action: 'clearFrontendEditor',
              value: { disabled: false, status: 'idle' }
            });
          }, 1500);
        });
    }
  }
});
